html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

svg {
  display: block;
}

.tools {
  z-index: 1;
  flex-direction: column;
  gap: 8px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: fixed;
  top: 16px;
  left: 16px;
}

.modal {
  z-index: 1;
  background: #00000026;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.modal-active {
  display: flex;
}

.modal_body {
  background: #fff;
  border-radius: 15px;
  flex-grow: 1;
  max-width: 90%;
  position: relative;
}

.modal_close {
  position: absolute;
  top: 8px;
  right: 12px;
}

.modal_content {
  scrollbar-width: thin;
  grid-template-columns: repeat(auto-fill, minmax(112px, 1fr));
  gap: 4px;
  max-height: 80vh;
  padding: 8px;
  display: grid;
  overflow: auto;
}

.flag {
  text-align: center;
  cursor: pointer;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 100px;
  display: flex;
}

.flag_emoji {
  pointer-events: none;
  font-size: 56px;
  line-height: 1;
}

.flag_description {
  pointer-events: none;
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 10px;
  display: inline-block;
  overflow: hidden;
}

.picked {
  text-shadow: 0 0 15px #00000040;
  cursor: default;
  font-size: 80px;
  line-height: 1;
}

.inputtxt {
  border: 1px solid #666;
  border-radius: 4px;
  padding: 4px 8px;
}

.inputtxt:focus-visible {
  border-color: #3c6bc2;
  outline: 1px solid #3c6bc2;
}
